/*Ussage:
font-size:rem(32, 16); // => font-size:2rem;
*/
@media screen and (max-width: 1340px) {
  html {
    font-size: 15px; } }

@media screen and (max-width: 1250px) {
  html {
    font-size: 14px; } }

@media screen and (max-width: 1160px) {
  html {
    font-size: 13px; } }

@media screen and (max-width: 1080px) {
  html {
    font-size: 12px; } }

@media screen and (max-width: 991px) {
  html {
    font-size: 16px; }
  .subscribe-list .card-box {
    width: calc((100% / 2) - 1.25rem); } }

@media screen and (max-width: 810px) {
  html {
    font-size: 15px; } }

@media screen and (max-width: 760px) {
  html {
    font-size: 13px; } }

@media screen and (max-width: 640px) {
  html {
    font-size: 16px; }
  header .toolbar-wrap {
    padding: 1.25rem 0; }
  header .toolbar-wrap .links-box a {
    font-size: 0.875rem; }
  header .toolbar-wrap .links-box a .icon-phone {
    padding-right: 0.4375rem;
    font-size: 1.25rem; }
  header .toolbar-wrap .logo a,
  footer .main-info .toolbar-wrap .logo a {
    max-width: 8.75rem; }
  .main-intro .data-wrap .intro-img {
    width: 100%;
    padding-bottom: 0.625rem;
    text-align: center;
    order: 1; }
  .main-intro .data-wrap .intro-text {
    width: 100%;
    padding: 0.9375rem 0;
    order: 2; }
  .main-intro .data-wrap .intro-text .title-box {
    text-align: center; }
  .title-box h1, .title-box h2 {
    font-size: 1.75rem; }
  .title-box h3 {
    font-size: 1.375rem; }
  .title-box p {
    padding-top: 0.3125rem;
    font-size: 0.875rem; }
  .main-intro .data-wrap .intro-text .links-box {
    padding-top: 0.9375rem;
    text-align: center; }
  .main-intro .data-wrap .intro-text .links-box a {
    margin-top: 0.3125rem;
    margin-right: 0.3125rem;
    max-width: 9.6875rem; }
  .subscribe-formats {
    padding: 3.125rem 0; }
  .subscribe-list {
    padding-top: 0.9375rem; }
  .subscribe-list .card-box {
    width: calc(100% - 1.25rem); }
  .subscribe-list .card-icon {
    margin-bottom: 1.25rem; }
  .subscribe-list .card-box .price {
    padding-top: 1.25rem; }
  .subscribe-list .card-box .price p {
    font-size: 1.75rem; }
  .subscribe-list .card-box .btn-wrap {
    padding-top: 1.25rem; }
  .easy-establishing .data-wrap {
    padding: 1.5625rem 1.25rem; }
  .easy-establishing .data-wrap .links-box {
    padding-top: 0.9375rem; }
  .easy-establishing .data-wrap .links-box a {
    margin: 0.3125rem;
    max-width: 13.125rem; }
  .easy-establishing .data-wrap .links-box a img {
    max-height: 3.875rem; }
  .easy-establishing .data-wrap .title-box h1 span:before, .easy-establishing .data-wrap .title-box h2 span:before {
    height: 0.5625rem;
    left: 2.5rem;
    bottom: -0.625rem; }
  footer {
    margin-top: 3.125rem; }
  footer .main-info .toolbar-wrap .links-box a {
    margin-left: 1.5625rem;
    margin-bottom: 0.3125rem; }
  footer .main-info {
    padding: 1.5625rem 0; }
  footer .main-info .toolbar-wrap {
    padding-bottom: 0; }
  footer .extra-info .text {
    padding-bottom: 0; }
  footer .extra-info {
    padding: 0.625rem 0; } }

