/*Ussage:
font-size:rem(32, 16); // => font-size:2rem;
*/
html {
  font-size: 16px;
  overflow-x: hidden; }

body {
  position: relative;
  min-width: 320px;
  color: #000000;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  backface-visibility: hidden;
  text-rendering: geometricPrecision; }

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

*,
*::before,
*::after {
  box-sizing: border-box; }

a, a:focus, a:hover {
  text-decoration: none; }

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0; }

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle; }

button {
  padding: 0;
  cursor: pointer; }

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none; }

select {
  -webkit-appearance: none; }

.container {
  width: 100%;
  max-width: 73.125rem;
  margin: 0 auto;
  padding: 0 0.9375rem; }

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 2.8125rem;
  padding: 0.625rem 1.5625rem;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1;
  color: #FFFFFF;
  border-radius: 0.5rem;
  border: 1px solid #162136;
  background-color: #162136;
  transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out, background-color 0.3s ease-in-out; }
  .main-btn .icon-arrow {
    display: block;
    padding-left: 1.625rem;
    font-size: 0.875rem; }
  .main-btn:hover {
    color: #162136;
    background-color: transparent; }

header .toolbar-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.75rem 0; }
  header .toolbar-wrap .logo {
    padding-right: 1.25rem; }
    header .toolbar-wrap .logo a {
      display: block;
      max-width: 11.9375rem; }
  header .toolbar-wrap .links-box a {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 1.25rem;
    line-height: 1.6;
    color: #162136;
    white-space: nowrap;
    transition: opacity 0.3s ease-in-out; }
    header .toolbar-wrap .links-box a .icon-phone {
      display: block;
      padding-right: 1.125rem;
      font-size: 1.75rem; }
    header .toolbar-wrap .links-box a:hover {
      opacity: .8; }

.title-box {
  text-align: center; }
  .title-box h1, .title-box h2 {
    margin: 0;
    font-weight: 500;
    font-size: 2.25rem;
    line-height: 1.44;
    letter-spacing: 0.0125rem;
    color: #162136; }
  .title-box h3 {
    margin: 0;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 1.33;
    color: #162136; }
  .title-box p {
    margin: 0;
    padding-top: 0.375rem;
    font-weight: normal;
    font-size: 1.125rem;
    line-height: 1.78;
    letter-spacing: 0.0125rem;
    color: #5A7184; }

.main-intro {
  padding: 0.625rem 0; }
  .main-intro .data-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
    .main-intro .data-wrap .intro-text {
      width: 56.9%;
      padding-top: 3.375rem;
      padding-right: 0.9375rem; }
      .main-intro .data-wrap .intro-text .title-box {
        text-align: unset; }
      .main-intro .data-wrap .intro-text .links-box {
        padding-top: 1.75rem; }
        .main-intro .data-wrap .intro-text .links-box a {
          display: inline-block;
          margin-top: 0.75rem;
          margin-right: 0.75rem;
          max-width: 10.6875rem; }
    .main-intro .data-wrap .intro-img {
      width: 43.1%; }

.subscribe-formats {
  padding: 6.25rem 0; }

.subscribe-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -0.625rem;
  padding-top: 3.375rem; }
  .subscribe-list .info-wrap {
    flex: auto; }
  .subscribe-list .card-icon {
    width: 4rem;
    height: 4rem;
    margin-bottom: 1.4375rem;
    border-radius: 50%;
    border: 1px solid #d0e0f7;
    overflow: hidden; }
    .subscribe-list .card-icon img {
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .subscribe-list .card-box {
    display: flex;
    flex-direction: column;
    width: calc((100% / 3) - 1.25rem);
    margin: 0.625rem;
    padding: 2rem;
    border: 1px solid #E5EAF4;
    box-shadow: 0 3.125rem 3.125rem rgba(0, 0, 0, 0.05);
    border-radius: 1rem; }
    .subscribe-list .card-box .title-box {
      text-align: unset; }
    .subscribe-list .card-box .price {
      padding-top: 2.5rem; }
      .subscribe-list .card-box .price p {
        margin: 0;
        font-weight: normal;
        font-size: 2.25rem;
        line-height: 1.33;
        letter-spacing: 0.0125rem;
        color: #162136; }
    .subscribe-list .card-box .btn-wrap {
      padding-top: 2.5rem; }

