footer {
  margin-top: 5.6875rem;
  background: #F2F2F2; }
  footer .main-info {
    padding: 2.5rem 0; }
    footer .main-info .toolbar-wrap {
      display: flex;
      justify-content: space-between;
      padding-bottom: 0.5rem; }
      footer .main-info .toolbar-wrap .logo a {
        display: block;
        max-width: 11.9375rem; }
      footer .main-info .toolbar-wrap .links-box a {
        display: inline-block;
        margin-left: 2.625rem;
        margin-bottom: 0.625rem;
        font-size: 1.75rem;
        color: #162136;
        transition: opacity 0.3s ease-in-out; }
        footer .main-info .toolbar-wrap .links-box a:hover {
          opacity: 0.7; }
  footer .extra-info {
    padding: 1.125rem 0;
    border-top: 0.5px solid #A4A9B5; }
    footer .extra-info .text {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin: 0 -0.3125rem;
      padding-bottom: 1.5625rem; }
      footer .extra-info .text p {
        margin: 0;
        padding: 0.3125rem;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 1.5;
        color: #162136; }
        footer .extra-info .text p a {
          color: inherit;
          transition: opacity 0.3s ease-in-out; }
          footer .extra-info .text p a:hover {
            opacity: 0.7; }
