$main-font-size: 16px;

@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

/// Convert value to em.
@function rem($value, $font-size: $main-font-size) {
  @return (strip-unit($value) / strip-unit($font-size)) + rem;
}

/*Ussage:
font-size:rem(32, 16); // => font-size:2rem;
*/

$Inter: 'Inter', sans-serif;
$OpenSans: 'Open Sans', sans-serif;
$transition: .3s ease-in-out;


$black: #000000;
$white: #ffffff;

:root {

}

html {
  font-size: $main-font-size;
  overflow-x: hidden;
}

body {
  position: relative;
  min-width: 320px;
  color: $black;
  font-family: $Inter;

  -webkit-font-smoothing: subpixel-antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  backface-visibility: hidden;
  text-rendering: geometricPrecision;
}

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a, a:focus, a:hover {
  text-decoration: none;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
}

.container {
  width: 100%;
  max-width: rem(1170);
  margin: 0 auto;
  padding: 0 rem(15);
}

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: rem(45);
  padding: rem(10) rem(25);
  font-family: $OpenSans;
  font-weight: bold;
  font-size: rem(18);
  line-height: 1;
  color: #FFFFFF;
  border-radius: rem(8);
  border: 1px solid #162136;
  background-color: #162136;
  transition: color $transition, border-color $transition, background-color $transition;

  .icon-arrow {
    display: block;
    padding-left: rem(26);
    font-size: rem(14);
  }

  &:hover {
    color: #162136;
    background-color: transparent;
  }
}

header {
  .toolbar-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(28) 0;

    .logo {
      padding-right: rem(20);

      a {
        display: block;
        max-width: rem(191);
      }
    }

    .links-box {
      a {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: rem(20);
        line-height: 1.6;
        color: #162136;
        white-space: nowrap;
        transition: opacity $transition;

        .icon-phone {
          display: block;
          padding-right: rem(18);
          font-size: rem(28);
        }

        &:hover {
          opacity: .8;
        }
      }
    }
  }
}

.title-box {
  text-align: center;

  h1, h2 {
    margin: 0;
    font-weight: 500;
    font-size: rem(36);
    line-height: 1.44;
    letter-spacing: rem(0.2);
    color: #162136;
  }

  h3 {
    margin: 0;
    font-weight: 500;
    font-size: rem(24);
    line-height: 1.33;
    color: #162136;
  }

  p {
    margin: 0;
    padding-top: rem(6);
    font-weight: normal;
    font-size: rem(18);
    line-height: 1.78;
    letter-spacing: rem(0.2);
    color: #5A7184;
  }
}

.main-intro {
  padding: rem(10) 0;

  .data-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .intro-text {
      width: 56.9%;
      padding-top: rem(54);
      padding-right: rem(15);

      .title-box {
        text-align: unset;
      }

      .links-box {
        padding-top: rem(28);

        a {
          display: inline-block;
          margin-top: rem(12);
          margin-right: rem(12);
          max-width: rem(171);
        }
      }
    }

    .intro-img {
      width: (100% - 56.9%);
    }
  }
}

.subscribe-formats {
  padding: rem(100) 0;
}

.subscribe-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 rem(-10);
  padding-top: rem(54);

  .info-wrap {
    flex: auto;
  }

  .card-icon {
    width: rem(64);
    height: rem(64);
    margin-bottom: rem(23);
    border-radius: 50%;
    border: 1px solid #d0e0f7;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .card-box {
    display: flex;
    flex-direction: column;
    width: calc((100% / 3) - #{rem(20)});
    margin: rem(10);
    padding: rem(32);
    border: 1px solid #E5EAF4;
    box-shadow: 0 rem(50) rem(50) rgba(0, 0, 0, 0.05);
    border-radius: rem(16);

    .title-box {
      text-align: unset;
    }

    .price {
      padding-top: rem(40);

      p {
        margin: 0;
        font-weight: normal;
        font-size: rem(36);
        line-height: 1.33;
        letter-spacing: rem(0.2);
        color: #162136;
      }
    }

    .btn-wrap {
      padding-top: rem(40);
    }
  }
}

