          &:before {
            content: '';
            height: rem(12);
            position: absolute;
            left: rem(66);
            right: 0;
            bottom: rem(-15);
            background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTU2IiBoZWlnaHQ9IjEyIiB2aWV3Qm94PSIwIDAgMTU2IDEyIiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgo8cGF0aCBkPSJNMSA3LjA0NTQ2QzEgNy4wNDU0NiA5Mi41ODMzIC02LjA2NDg1IDE1NSAxMC4wMDU5IiBzdHJva2U9IiNGMTUyMjMiIHN0cm9rZS13aWR0aD0iMyIvPgo8L3N2Zz4K);
            background-repeat: no-repeat;
            background-position: center;
