footer {
  margin-top: rem(91);
  background: #F2F2F2;

  .main-info {
    padding: rem(40) 0;
    .toolbar-wrap {
      display: flex;
      justify-content: space-between;
      padding-bottom: rem(8);

      .logo {
        a {
          display: block;
          max-width: rem(191);
        }
      }

      .links-box {
        a {
          display: inline-block;
          margin-left: rem(42);
          margin-bottom: rem(10);
          font-size: rem(28);
          color: #162136;
          transition: opacity $transition;

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }

  .extra-info {
    padding: rem(18) 0;
    border-top: 0.5px solid #A4A9B5;

    .text {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin: 0 rem(-5);
      padding-bottom: rem(25);

      p {
        margin: 0;
        padding: rem(5);
        font-weight: 400;
        font-size: rem(14);
        line-height: 1.5;
        color: #162136;

        a {
          color: inherit;
          transition: opacity $transition;

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
}